import React, { Component } from 'react'
import Content from '..//Content/Content'
import './Popular.css'
import {POPULAR_URL} from '../config'
import {handlerLoading} from '../handlerLoading/handlerLoading'

class Popular extends Component {
    state = {
        results: [],
        page: 1
    }

    async componentDidMount() {
        handlerLoading.call(null, this, POPULAR_URL)
    }
     
    render() {
        console.log("render")
        return (
            <>
                <h1 className="display-1 mx-auto">Popular</h1>
                <div className="container-fluid">
                    <div className="card-deck row justify-content-center my-5">
                        <Content 
                            results={this.state.results}
                            path="/"
                        />
                    </div>
                </div>
                <button 
                className="btn btn-info mx-auto center"
                onClick={handlerLoading.bind(null, this, POPULAR_URL)}>Load more..</button>  
            </>
        )
    }
}


export default Popular;