import React, { Component } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import { SEARCH_URL } from '../../config'
import './Form.css'
import Content from '../../Content/Content'
import LoaderButton from '../../LoaderButton/LoaderButton'

let timer;

class Form extends Component {

    state = {
        inputValue: "",
        page: 1,
        results: undefined,
        allPages: 0
    }

    valueTarget = ({ target }) => {
        this.setState({ inputValue: target.value, page: 1 })
        clearTimeout(timer);
        if (target.value === "") {
            this.setState({ results: undefined, allPages: 0, page: 1})
            return
        }
        timer = setTimeout(this.doSearch.bind(this, target.value), 500);
    }

    doSearch = async (val, page = false) => {
        if(page) {
            const url = `${SEARCH_URL(val)}&page=${page}`;
            const fetched = await fetch(url);
            const data = await fetched.json();
            this.setState( state => {
                return {
                    results: [...state.results, ...data.results], 
                    page: state.page + 1
                }
            })
        } else {
            const fetched = await fetch(SEARCH_URL(val));
            const data = await fetched.json();
            this.setState({results: [...data.results], allPages: data.total_pages})
        }

        return false
    }

    handlerLoading = () => {
        const nextPage = this.state.page + 1;
        this.doSearch(this.state.inputValue, nextPage)
    }

    handlerGoUp = () => {
        window.scrollTo({top: 0, behavior: "smooth"});
    }

    leftData = () => {
        return this.state.allPages > this.state.page ? true : false
    }


    render() {
        return <>
            <form className="form-inline order-0 center">
                <input className="form-control mr-sm-2" 
                type="search" value={this.state.inputValue} 
                placeholder="Search film" aria-label="Search" 
                onChange={this.valueTarget} 
                />
            </form>
            {this.state.results
                ?
                <div className="container-fluid">
                    <div className="card-deck row justify-content-center my-5">
                        {
                            this.state.results.length > 0
                                ?
                                <Content
                                    results={this.state.results}
                                    path="/"
                                />
                                :
                                "not found"
                        }
                    </div>
                </div>
                : ""
            }
            {
                this.state.results
                ?
                <LoaderButton
                    handlerLoading={this.handlerLoading}
                    handlerGoUp={this.handlerGoUp}
                    leftData={this.leftData}
                    data={this.state.results.length}
                />
                :
                ""
            }

        </>
    }
}

export default Form
