export const handlerLoading = async (self, urlFetch) => {
    const fetched = await fetch(urlFetch(self.state.page));
    const data = await fetched.json()
    self.setState((state) => {
        return {
            results: [...state.results, ...data.results],
            page: state.page + 1
        }
    }, console.log(self.state))
    return 
}
